import React from 'react'
import { connect } from 'react-redux'
import { push } from 'react-router-redux'
import styled from 'styled-components'
import Notification from 'rc-notification'
import 'rc-notification/assets/index.css'
import { authFail } from '../../actions/auth'
import Navbar from '../../components/Navbar'
import HeaderNav from '../../components/HeaderNav'

let notification = null
Notification.newInstance({}, (n) => notification = n)

const Content = styled.div`
  padding-top: 50px;
`

class HomeContainer extends React.Component {
  //  sessionStorage
  logout () {
    const { dispatch } = this.props
    dispatch(authFail())
    this.notifice('')
    dispatch(push('/login'))
  }

  notifice (val) {
    notification.notice({
      content: <span>{ val }</span>,
      duration: 2,
      style: {
        transform: 'translateX(-50%)',
        borderRadius: '5px',
        background: 'rgba(0, 0, 0, .8)',
        color: '#fff'
      }
    })
  }

  render () {
    const { token, userInfo } = this.props
    return (
      <div className='home'>
        <HeaderNav title='' />
        <Content>
          <img src={userInfo.avatar_url} alt={userInfo.loginname} />
          <p>{userInfo.loginname}</p>
          <p>token: {token}</p>

          <button onClick={this.logout.bind(this)}></button>
        </Content>
        <Navbar />
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { auth } = state
  const { userInfo, token } = auth

  return {
    token,
    userInfo
  }
}

const Home = connect(mapStateToProps)(HomeContainer)

export default Home



